<template>
  <ul class="navigation-bar">
    <navigation-bar-item
      v-for="(item, index) in itemsCmpt" 
      :active="item.active"
      :key="index"
      :text="item.text"
      :route="item.route"
    />
  </ul>
</template>

<script>
import NavigationBarItem from './NavigationBarItem'
export default {
  components: { NavigationBarItem },
  data: () => ({
    items: [
      { text: 'Agende um horrio', route: 'barbershops' },
      { text: 'Favoritos', route: 'favorits' },
      { text: 'Meus agendamentos', route: 'site-schedule' },
      { text: 'Entrar', route: 'login' },
      { text: 'Para barbeiros', route: 'login-barber' }
    ]
  }),
  computed: {
    itemsCmpt () {
      let routeName = this.$route.name
      if (routeName == 'index') routeName = 'barbershops'
      return this.items.map(i => {
        i.active = i.route === routeName
        return i
      })
    }
  }
}
</script>

<style scoped>
.navigation-bar {
  background: #fff;
  height: 80px;
  display: flex;
  align-content: end;
  justify-content: flex-end;
  padding: 0 !important;
}
</style>