<template>
  <div class="contact">
    <app-title class="titulo" text="Entre em contato conosco" />
    <div class="form">
      <textarea-field
        label=""
        placeholder="Digite sua mensagem"
        :solo="true"
        class="input-box"
        :hideDetails="true"
        @onKeyUp="setMessage"
      />
      <v-btn
        class="btn-enviar"
        @click="sendMessage"
      >
        Enviar
      </v-btn>
    </div>
    <contact-dialog ref="contactDialog" />
  </div>
</template>

<script>
import ContactDialog from './ContactDialog'
import TextareaField from '@/components/globais/form/textarea-field/TextareaField'
import AppTitle from '@/components/site/Title'
export default {
  components: { ContactDialog, TextareaField, AppTitle },
  data: () => ({
    message: null
  }),
  methods: {
    setMessage (value) {
      this.message = value
    },
    sendMessage () {
      this.$refs.contactDialog.showDialog(this.message)
    }
  }
};
</script>

<style scoped lang="scss">
@import '@/scss/Theme.scss';
.contact {
  h1 {
    font-size: 1.2rem;
    text-align: left;
    font-weight: 400;
  }
  .form {
    position: relative;
    padding-top: 20px;
    margin-top: 12px;
    .input-box {
      width: calc(100% - 80px);
      background: #fff;
    }
    .btn-enviar {
      background: $accent2 !important;
      border-radius: 0 !important;
      height: 48px;
      padding: 0 40px;
      position: absolute;
      top: 0;
      right: 0;
    }
  }
}
</style>
