<template>
  <v-dialog v-model="dialog" max-width="350">
    <div class="contact-dialog primaryDark" dark>
      <app-title text="Entre em contato conosco" class="font-weight-regular" />
      <div class="form mt-8">
        <div>
          <label class="label white--text">Nome:</label>
          <input-field
            label=""
            id="name"
            placeholder="Informe seu nome"
            :solo="true"
            :dark="true"
            class="input-box"
            :hideDetails="true"
            @onKeyUp="setData"
          />
        </div>
        <div class="mt-4">
          <label class="white--text label">E-mail:</label>
          <input-field
            label=""
            id="email"
            placeholder="Informe seu e-mail"
            :solo="true"
            :dark="true"
            class="input-box"
            :hideDetails="true"
            @onKeyUp="setData"
          />
        </div>
        <div class="mt-4">
          <label class="white--text label">Mensagem:</label>
          <textarea-field
            class="input-box"
            :hideDetails="true"
            id="message"
            label=""
            placeholder="Digite sua mensagem"
            :solo="true"
            :dark="true"
            :rows="4"
            :value="message"
            @onKeyUp="setData"
          />
        </div>
        <btn-primary
          text="Enviar"
          :loading="loading"
          class="mt-8 btn-send" 
          @click="sendMessage"
        />
        <btn-text text="Cancelar" class="white--text mt-4" @clicked="cancel" />
      </div>
    </div>
  </v-dialog>
</template>

<script>
import AppTitle from '@/components/site/Title'
import BtnPrimary from '@/components/site/buttons/BtnPrimary'
import BtnText from '@/components/globais/buttons/text/BtnText'
import InputField from '@/components/globais/form/input-field/InputField'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import TextareaField from '@/components/globais/form/textarea-field/TextareaField'
export default {
  components: { AppTitle, BtnPrimary, BtnText, InputField, TextareaField },
  mixins: [ SnackbarMixin ],
  data: () => ({
    dialog: false,
    name: null,
    email: null,
    loading: false,
    message: ''
  }),
  methods: {
    cancel () {
      this.resetForm()
      this.dialog = false
    },
    resetForm () {
      this.name =  ''
      this.email = ''
      this.message = ''
    },
    sendMessage () {
      if (this.name.length === 0) {
        this.showSnackbarErro('Por favor, informe seu nome!')
      } else if (this.email.length === 0)  {
        this.showSnackbarErro('Por favor, informe seu e-mail!')
      } else if (this.message.length === 0)  {
        this.showSnackbarErro('Por favor, informe sua mensagem!')
      } else {
        this.loading = true
        this.$axios.post(`/contact`, { 
          message: this.message,
          name: this.name,
          email: this.email 
        }).then(response => {
          if (response.data.codigo === 200) {
            this.showSnackbar('Sua mensagem foi encaminhada com sucesso!')
            this.resetForm()
            this.dialog = false
          } else this.setError()
        }).catch (() => this.setError())
        .finally(() => this.loading = false)
      }
    },
    setError () {
      this.showSnackbarErro('No foi possvel enviar sua mensagem!')
    },
    setData (value, id) {
      this.$data[id] = value
    },
    showDialog (message) {
      this.dialog = true
      this.resetForm()
      setTimeout(() => {
        this.message = message
      }, 300)
    }
  }
}
</script>

<style scoped>
.contact-dialog { padding: 30px; }
.btn-send { width: 100%; }
</style>