<template>
  <v-btn
    class="accent btn-primary d-block primary--text text-none"
    light
    @click="click"
  >
    <template v-if="loading">
      <v-progress-circular
        indeterminate
        width="2"
        color="primaryDark"
        small
      ></v-progress-circular>
    </template>
    <template v-else>
      <v-icon v-if="icon" class="mr-2">{{ icon }}</v-icon> {{ text }}
    </template>
  </v-btn>
</template>

<script>
export default {
  props: {
    icon: { default: null },
    text: { default: '' },
    loading: { default: false }
  },
  methods: {
    click () {
      this.$emit('click')
    }
  }
}
</script>

<style scoped>
.btn-primary { border-radius: 0; height: 48px !important; }
</style>