<template>
  <li class="navigation-bar-item">
    <v-btn 
      class="text-none btn-navigation-bar"
      :class="isActived"
      @click="goTo(route)"
    >
      {{ text }}
    </v-btn>
  </li>
</template>

<script>
import NavigationMixin from '@/mixins/NavigationMixin'
export default {
  mixins: [ NavigationMixin ],
  props: {
    active: { default: false },
    text: { default: '' },
    route: { default: '' }
  },
  computed: {
    isActived () {
      return this.active ? 'actived' : ''
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/scss/Theme.scss';
.navigation-bar-item { display: flex; }
.btn-navigation-bar {
  height: 100% !important;
  border-radius: 0; 
  padding: 0 24px !important;
  &:hover, &.actived {
    background: $accent2 !important;
  }
}
</style>