<template>
  <v-dialog v-model="dialog" max-width="350">
    <div class="indicate-form primaryDark" dark>
      <app-title text="Indicar estabelecimento" class="app-title" />
      <div class="mt-8">
        <div>
          <label class="label white--text">Estabelecimento:</label>
          <input-field
            id="barbershopName"
            :dense="true"
            :hideDetails="true"
            label=""
            :dark="true"
            :solo="true"
            placeholder="Nome do estabelecimento"
            @onKeyUp="setData" 
          />
        </div>
        <div class="mt-4">
          <label class="label white--text">Responsvel:</label>
          <input-field
            id="barberName"
            :dense="true"
            :hideDetails="true"
            label=""
            :solo="true"
            :dark="true"
            placeholder="Algum para entrarmos em contato"
            @onKeyUp="setData"
          />
        </div>
        <div class="mt-4">
          <label class="label white--text">Telefone:</label>
          <phone-field
            id="phone"
            :dense="true"
            :hideDetails="true"
            label=""
            :solo="true"
            :dark="true"
            placeholder="Telefone"
            @onKeyUp="setData" 
          />
        </div>
      </div>
      <btn-primary
        text="Indicar"
        class="btn-indicate mt-6" 
        :loading="btnLoading"
        @click="sendIndication"
      />
    </div>
  </v-dialog>
</template>

<script>
import AppTitle from '@/components/site/Title'
import BtnPrimary from '@/components/site/buttons/BtnPrimary'
import PhoneField from '@/components/globais/form/phone-field/PhoneField'
import InputField from '@/components/globais/form/input-field/InputField'
import SnackbarMixin from '@/mixins/SnackbarMixin'
export default {
  components: { AppTitle, BtnPrimary, InputField, PhoneField },
  mixins: [ SnackbarMixin ],
  data: () => ({
    barbershopName: null,
    barberName: null,
    btnLoading: false,
    dialog: false,
    phone: null,
  }),
  methods: {
    isFormValid () {
      if (!this.barberName) {
        this.showSnackbarErro('Por favor, informe o nome do responsvel!')
        return false
      } else if (!this.barbershopName) {
        this.showSnackbarErro('Por favor, informe o nome do estabelecimento!')
        return false
      } else if (!this.phone || this.phone.length < 15) {
        this.showSnackbarErro('Por favor, informe um telefone vlido!')
        return false
      } 
      return true
    },
    resetForm () {
      this.barbershopName = null
      this.barberName = null
      this.btnLoading = false
      this.phone = null
    },
    sendIndication () {
      if (this.isFormValid()) {
        this.btnLoading = true
        this.$axios.post(`/indicate`, { 
          barbershopName: this.barbershopName,
          barberName: this.barberName,
          phone: this.phone 
        }).then(response => {
          if (response.data.codigo === 200) {
            this.showSnackbar('Sua indicao foi encaminhada com sucesso!')
            this.resetForm()
            this.dialog = false
          } else this.setError(response.data.corpo)
        }).catch (() => this.setError('No foi possvel enviar sua mensagem!'))
        .finally(() => this.btnLoading = false)
      }
    },
    setData (value, id) {
      this.$data[id] = value
    },
    setError (msg) {
      this.showSnackbarErro(msg)
    },
    showDialog () {
      this.resetForm()
      this.dialog = true
    }
  }
}
</script>

<style lang="scss" scoped>
.indicate-form {
  padding: 20px;
  width: 385px;
  max-width: 100%;
  height: max-content;
  display: block;
  background: #fff;
  .app-title { font-weight: 400; }
  p { opacity: .8; }
  .btn-indicate { margin: auto; padding: 0 48px; }
}
</style>